import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';
import APIcon from '../common/APIcon/APIcon';
import APButton from '../common/APButton';
import './TransferRequestItem.css';
import { styleNames } from '../../propTypes/commonComponent';

const transferRequestItemClasses = getBEMClasses('transfer-request-item');

const TransferRequestItem = (props, context) => {
  const {
    iconFileName,
    ProductSymbol,
    ProductFullName,
    Amount,
    RequestorUsername,
    approveTransferRequest,
    rejectTransferRequest
  } = props;

  return (
    <div className={transferRequestItemClasses()}>
      <div className={transferRequestItemClasses('details')}>
        <div className={transferRequestItemClasses('type-icon')}>
          <APIcon
            name={'send'}
            customClass={transferRequestItemClasses('icon')}
          />
        </div>
        <div className={transferRequestItemClasses('product-icon')}>
          <ProductIconContainer iconFileName={iconFileName} size={35} />
        </div>
        <div className={transferRequestItemClasses('description')}>
          {context.t('Send')}{' '}
          {Amount + ' ' + ProductSymbol + ' ' + ProductFullName}{' '}
          {context.t('to')} {RequestorUsername}
        </div>
      </div>
      <div className={transferRequestItemClasses('buttons')}>
        <APButton
          onClick={() => approveTransferRequest()}
          customClass={transferRequestItemClasses()}
          styleName={styleNames.additive}>
          {context.t('Approve')}
        </APButton>
        <APButton
          onClick={() => rejectTransferRequest()}
          customClass={transferRequestItemClasses()}
          styleName={styleNames.subtractive}>
          {context.t('Reject')}
        </APButton>
      </div>
    </div>
  );
};

TransferRequestItem.contextTypes = {
  t: PropTypes.func.isRequired
};

export default TransferRequestItem;



