import { connect } from 'react-redux';
import TransferRequestItemComponent from './TransferRequestItemComponent';
import { getProductByIdSelector } from '../../redux/selectors/productPositionSelectors';
import {
  approveTransferRequest,
  rejectTransferRequest
} from '../../redux/actions/transferRequestActions';

const mapStateToProps = (state, ownProps) => {
  const {
    iconFileName,
    ProductSymbol,
    ProductFullName
  } = getProductByIdSelector(state, ownProps.item.ProductId);

  return {
    iconFileName,
    ProductSymbol,
    ProductFullName,
    RequestorUsername: ownProps.item.RequestorUsername,
    Amount: ownProps.item.Amount
  };
};

const mapDispatchToProps = (dispatch, ownProps) => ({
  approveTransferRequest: () =>
    dispatch(approveTransferRequest(ownProps.item.RequestCode)),
  rejectTransferRequest: () =>
    dispatch(rejectTransferRequest(ownProps.item.RequestCode))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TransferRequestItemComponent);



