import React from 'react';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import Spinner from '../common/Spinner/Spinner';
import TransferRequestItem from './TransferRequestItemContainer';
import PaginationContainer from '../common/Pagination/PaginationContainer';
import './TransferRequestList.css';

const transferRequestListClasses = getBEMClasses('transfer-request-list');

const transferRequestList = props => {
  const items = props.filteredItems.map(item => {
    return <TransferRequestItem key={item.RequestCode} item={item} />;
  });

  const pageSize = 3;
  const totalPages = Math.ceil(items.length / pageSize || items.length);

  const transferRequests = totalPages ? (
    <PaginationContainer
      componentName="transferRequest"
      content={page => items.slice(pageSize * page, pageSize * (page + 1))}
      totalPages={totalPages}
    />
  ) : null;

  return (
    <div
      className={
        totalPages
          ? transferRequestListClasses()
          : transferRequestListClasses('empty')
      }>
      {props.fetching ? <Spinner /> : transferRequests}
    </div>
  );
};

export default transferRequestList;



