import { connect } from 'react-redux';
import TransferRequestListComponent from './TransferRequestListComponent';
import { transfersRequestSelector } from '../../redux/selectors/transfersTableSelectors';

const mapStateToProps = state => {
  const filteredItems = transfersRequestSelector(state);
  return {
    filteredItems
  };
};

export default connect(mapStateToProps)(TransferRequestListComponent);



